.switchContainer {
  width: 50px;
  height: 30px;
  border-radius: 20px;
  padding: 2px;
  display: flex;
  align-items: center;
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 90;
  transition: all ease 0.5s;

  div {
    width: 26px;
    height: 26px;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all ease 0.3s;
  }
}

.darkSwitch {
  justify-content: right;
  background-color: #cccccc;
  box-shadow: 
    -2px 0px 3px rgba(245, 220, 49, 0.25),
    2px 0px 3px rgba(245, 220, 49, 0.25), 
    0px -2px 3px rgba(245, 220, 49, 0.25),
    0px 2px 3px rgba(245, 220, 49, 0.25);

  div {
    background-color: #2a2c35;
    color: #f5dc31;
  }
}

.lightSwitch {
  justify-content: left;
  background-color: #cccccc;
  box-shadow: 
    -2px 0px 3px rgba(58, 69, 246, 0.25),
    2px 0px 3px rgba(58, 69, 246, 0.25),
    0px -2px 3px rgba(58, 69, 246, 0.25),
    0px 2px 3px rgba(58, 69, 246, 0.25);

  div {
    background-color: #ffffff;
    color: #3a45f6;
  }
}

@media screen and (min-width: 768px) {
  .switchContainer {
    top: 15px;
    right: 15px;
  }
}

@media screen and (min-width: 1220px) {
  .switchContainer {
    top: 30px;
    right: 30px;
  }
}

@media screen and (min-width: 1920px) {
  .switchContainer {
    top: 50px;
    right: 60px;
  }
}
