.projectsContainer {
  width: 100%;
  min-height: 100vh;
  padding: 0 6%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
    
    .txtProjectsContainer{
      width: 100%;
      margin-bottom: 25px;

        h2{
          font-family: "Signika", sans-serif;
          font-size: 25px;
          margin-bottom: 15px;
        }

        p{
          font-size: 14px;
          line-height: 17px;
        }
    }

    .cardContainer{
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
    }
}

.darkTxtProject {
  h4,
  h1,
  h2,
  span,
  a {
    color: #f5dc31;
  }
  p {
    color: #ffffff;
  }
}

.lightTxtProject {
  h4,
  h1,
  h2,
  span,
  a {
    color: #3a45f6;
  }
  p{
    color: #646464;
  }
}

@media screen and (min-width: 768px) {
  .projectsContainer{
    padding: 0 13%;

      .txtProjectsContainer{
        margin-bottom: 70px;

          h2{
            font-size: 55px;
            margin-bottom: 20px;
          }

          p{
            font-size: 16px;
            line-height: 20px;
            max-width: 440px;
          }
      }
  }
}

@media screen and (min-width: 1220px){
  .projectsContainer{
    padding: 0;

      .txtProjectsContainer{
        padding-left: 15%;
      }
  }
}

@media screen and (min-width:1920px) {
  .projectsContainer{
    .txtProjectsContainer{
      padding: 0 340px;
        p{
          font-size: 20px;
          line-height: 24px;
        }
    }
  }
}