.contactContainer {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;

  .info {
    margin-bottom: 40px;
    text-align: center;
    h2 {
      font-family: "Signika", sans-serif;
      margin-bottom: 20px;
    }
    p {
      padding-left: 10px;
    }
    .networks {
      display: flex;
      justify-content: center;
      img {
        width: 30px;
        margin: 0 12.5px;
      }
    }

    &:last-child{
      margin-bottom: 0;
    }
  }
}

.darkTxt {
  h4,
  h1,
  h2,
  span {
    color: #f5dc31;
  }
  p {
    color: #ffffff;
  }
}

.lightTxt {
  h4,
  h1,
  h2,
  span {
    color: #3a45f6;
  }
  p {
    color: #646464;
  }
}

@media screen and (min-width: 768px){
  .contactContainer{
    .info{
      h2{
        font-size: 40px;
      }
      p{
        font-size: 20px;
      }
    }
  }
}

