.itsMeContainer {
  width: 100%;
  height: 100vh;
  padding: 0 7%;
  display: flex;
  justify-content: center;
  align-items: center;

  .headerContainer {

    .imgContainer {
      width: 160px;
      margin-bottom: 15px;

        img{
          width: 100%;
        }
    }

    .txtContainer {
      h2 {
        font-size: 30px;
        font-weight: 300;
      }
      h1 {
        font-family: "Signika", sans-serif;
        font-size: 60px;
        font-weight: 400;
      }
      p {
        max-width: 280px;
        font-size: 16px;
        font-weight: 300;
        line-height: 19px;
      }
      span {
        font-weight: 400;
        animation: spanAnimation 2s infinite;
      }
    }
  }
}

.darkHomeTxt {
  h4,
  h1,
  span {
    color: #f5dc31;
  }
  p,
  h2 {
    color: #ffffff;
  }
}

.lightHomeTxt {
  h4,
  h1,
  span {
    color: #3a45f6;
  }
  p,
  h2 {
    color: #646464;
  }
}

@keyframes spanAnimation {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.2;
  }
  100% {
    opacity: 1;
  }
}

@media screen and (min-width: 768px) {
  .itsMeContainer{
    padding: 0;

      .headerContainer{
        display: flex;
        justify-content: center;
        align-items: center;

          .imgContainer{
            width: 175px;
            margin: 0 25px 0 0;
          }

          .txtContainer{

            h1{
              font-size: 85px;
            }

            p{
              max-width: 382px;
              font-size: 20px;
              line-height: 24px;
            }
          }
      }      
  }
}

@media screen and (min-width: 1220px) {
  .itsMeContainer{
    .headerContainer{
      .txtContainer{
        p{
          max-width: 417px;
        }
      }
    }
  }
}

@media screen and (min-width: 1920px) {.itsMeContainer{
  .headerContainer{
    .txtContainer{
      h2{
        font-size: 35px;
      }
      p{
        max-width: 626px;
        font-size: 25px;
        line-height: 30px;
      }
    }
  }
}
}
