.skillsContainer {
  width: 100%;
  height: 100vh;
  padding: 0 7%;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;

  .meContainer {
    margin-bottom: 30px;

    h2 {
      font-family: "Signika", sans-serif;
      font-size: 25px;
      font-weight: 700;
      display: inline-block;
      margin-right: 8px;
      margin-bottom: 10px;
    }
    h4 {
      font-size: 14px;
      font-weight: 300;
      display: inline-block;
    }
    p {
      font-size: 14px;
      line-height: 18px;
    }
  }

  .skills {
    h2 {
      font-family: "Signika", sans-serif;
      font-size: 25px;
      font-weight: 700;
      margin-bottom: 10px;
    }
    .devSkills {
      padding: 0 10px 25px;

      div {
        margin-bottom: 10px;
        h4{
          font-weight: 300;
        }
        p {
          margin-left: 10px;
          span {
            font-weight: 300;
          }
        }
        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    .naturalSkills {
      display: none;
    }
  }

  .daysContainer {
    display: none;
  }
}

.darkTxt {
  h4,
  h1,
  h2,
  span {
    color: #f5dc31;
  }
  p {
    color: #ffffff;
  }
}

.lightTxt {
  h4,
  h1,
  h2,
  span {
    color: #3a45f6;
  }
  p {
    color: #646464;
  }
}

.darkDays {
  color: rgba(255, 255, 255, 0.1);
}

.lightDays {
  color: rgba(0, 0, 0, 0.25);
}

.iconDark {
  color: #f5dc31;
}

.iconLight {
  color: #3a45f6;
}

@media screen and (min-width: 768px) {
  .skillsContainer{
    padding: 0 13%;

      .meContainer{
        margin-bottom: 70px;

          h2{
            font-size: 55px;
            margin-bottom: 20px;
          }
          h4{
            font-size: 30px;
          }
          p{
            font-size: 16px;
            line-height: 19px;
          }
      }

      .skills{
        display: flex;
        align-items: center;
        flex-wrap: wrap;

        h2{
          width: 100%;
          font-size: 55px;
          margin-bottom: 20px;
        }

        .devSkills{
          padding: 0;
          margin-right: 85px;

            div{
              margin-bottom: 20px;
            } &:last-child{
              margin-bottom: 0;
            }
        }

        .naturalSkills{
          display: flex;
          align-items: center;
          ul {
            list-style: none;
            li {
              display: flex;
              margin-bottom: 22px;
              p {
                margin-right: 10px;
                span {
                  font-size: 12px;
                  font-weight: 300;
                }
              }
              &:last-child{
                margin-bottom: 0;
            } 
            }
          }          
        }
      }
  }
}

@media screen and (min-width: 1220px) {
  .skillsContainer{
    padding: 0 15%;

      .meContainer{
        margin-bottom: 55px;

          p{
            max-width: 530px;
          }
      }

      .skills{
        
        .devSkills{
          max-width: 500px;
          margin-right: 30px;
          display: flex;
          flex-wrap: wrap;
            
            div{
              width: 225px;
              margin-right: 25px;
            }
        }
      }

      .daysContainer{
        display: block;
        position: absolute;
        right: 30px;
          
          div{
            text-align: right;

              &:first-child{
                margin-bottom: 20px;
              }
          }
      }
  }
}

@media screen and (min-width: 1920px) {
  .skillsContainer{
    padding: 0 340px;

      .meContainer{
        margin-bottom: 120px;

          p{            
            font-size: 20px;
            max-width: 585px;
            line-height: 24px;
          }
      }

      .skills{
        .devSkills{
          margin-right: 100px;
        }
      }

      .daysContainer{
        right: 60px;
          div{
            font-size: 20px;
          }
      }
  }
}
