.cardProject{
    width: 140px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 10px;
    position: relative;

        h4{
            font-size: 16px;
            font-weight: 400;
            color: #ffffff;
            text-align: center;
            margin-bottom: 20px;
        }

        img{
            width: 112px;
            border-radius: 125px;
            margin-bottom: 10px;
        }

        .linksMobile{
            width: 100%;
            height: 45px;
            display: flex;
            justify-content: center;
            align-items: center;

                a{
                    font-size: 23px;
                    display: flex;
                    align-items: center;
                    margin: 0 15px;
                    color: #ffffff;
                    img{
                        width: 23px;
                        margin-bottom: 0;
                        border-radius: 0;
                    }
                }
        }

        .linksDesktop{
            display: none;
        }
        
}

@media screen and (min-width: 768px) {
    .cardProject{
        width: 250px;
        padding-top: 15px;
        margin: 10px;

            h4{
                font-size: 20px;
                margin-bottom: 35px;
            }
            img{
                width: 200px;
                margin-bottom: 0;
            }

            .linksMobile{
                height: 80px;
                a{
                    font-size: 40px;    

                        img{
                            width: 40px;
                        }
                }
            }
    }
}

@media screen and (min-width: 1220px) {
    .cardProject{
        padding-bottom: 40px;

        .linksMobile{
            display: none;
        }

        .linksDesktop{
            width: 100%;
            height: 80px;
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            bottom: 80px;
            box-shadow: 0px -4px 4px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(0, 0, 0, 0.25);

            a{
                font-size: 40px;
                display: flex;
                align-items: center;
                margin: 0 15px;
                color: #ffffff;
                img{
                    width: 40px;
                    margin-bottom: 0;
                    border-radius: 0;
                }
            }
        }
    }
    
}